.btn.btn--white, .btn.btn--black {
  font-size: 1rem;
  padding: 0.625rem 1.6875rem;
  transition: background-color 0.35s ease, color 0.5s ease;
  z-index: 20;
}

.btn.btn--black {
  border: 2px solid #2d2640;
  color: #2d2640;
}
.btn.btn--black:hover {
  background-color: #2b272f;
  color: #FFFFFF;
}

.btn.btn--white {
  background-color: #2d2640;
  border: 2px solid #fafafa;
  color: #fafafa;
}
.btn.btn--white:hover {
  background-color: #fafafa;
  color: #2d2640;
}
.btn.btn--white:focus-visible {
  outline: 3px dashed #FFFFFF;
  outline-offset: 3px;
}

.section--main {
  display: flex;
  flex-direction: column;
}

.section--main {
  gap: 1.5rem 0;
}

.gutter {
  display: flex;
  justify-content: space-between;
}

.line-white, .line-black {
  padding-top: 3.875rem;
  position: relative;
}
.line-white::before, .line-black::before {
  content: "";
  height: 1px;
  left: 0;
  position: absolute;
  top: 0;
  width: 9.5rem;
}

.header__wrapper, .hero__wrapper, .overview__wrapper, .cta__wrapper, .footer__wrapper {
  max-width: 100rem;
  margin: 0 auto;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  font-family: "Karla", sans-serif;
  margin: 0 auto;
  padding: 0;
}

a {
  outline: none;
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4 {
  font-family: "DM Serif Display", serif;
  margin: 0;
}

p {
  line-height: 1.5rem;
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2 {
  font-size: 4rem;
}

h2 {
  color: #2b272f;
  font-size: clamp(2.875rem, 4.9vw, 4rem);
  line-height: 4rem;
  z-index: 10;
}

h3 {
  color: #fafafa;
  font-size: clamp(2.25rem, 4.9vw, 3rem);
  z-index: 10;
}

h4 {
  color: #2b272f;
  font-size: 1.75rem;
}

.flex {
  display: flex;
}

.navbar > ul {
  gap: 0 1.6875rem;
}

.header {
  background-color: #FFFFFF;
  position: relative;
  z-index: 10;
}
.header__wrapper {
  align-items: center;
  display: flex;
  height: 5rem;
  justify-content: space-between;
  padding: 0 10.25rem 0;
  position: relative;
}
.header a:focus-visible,
.header input:focus-visible {
  outline: 3px dashed #000000;
  outline-offset: 3px;
}

.hero {
  background-color: #2d2640;
  margin-bottom: clamp(10rem, 17vw, 19.0625rem);
  position: relative;
}
.hero__wrapper {
  height: 37.5rem;
  padding: 6.5625rem 10.25rem 0;
}
.hero::before {
  content: url("../images/bg-pattern-intro-right-desktop.svg");
  position: absolute;
  transform: translateY(-14%);
  top: 0;
  right: 0;
}
.hero::after {
  content: url("../images/bg-pattern-intro-left-desktop.svg");
  bottom: 0;
  left: 0;
  position: absolute;
  transform: translateY(60%);
}
.hero--image {
  height: clamp(27.9365rem, 45.1vw, 40.625rem);
  z-index: 10;
}
.hero--image-mbl {
  display: none;
  max-width: 100%;
}

.overview {
  margin-bottom: 10.125rem;
}
.overview__wrapper {
  gap: 5.5625rem 0;
  padding: 3.75rem 10.25rem 0;
}
.overview__wrapper::before {
  left: 10.25rem;
}
.overview__icon {
  margin-bottom: 0.75rem;
}

.cta {
  background-color: #2d2640;
  margin-bottom: 9.375rem;
}
.cta__wrapper {
  padding: 0 10.25rem;
}
.cta__holder {
  align-items: center;
  height: 15.625rem;
  padding: 0 5rem;
  position: relative;
}
.cta__holder::before {
  content: url("../images/bg-pattern-how-we-work-desktop.svg");
  position: absolute;
  right: 0;
  top: 0;
}

.footer {
  background: url("../images/bg-pattern-footer-desktop.svg") no-repeat left top;
  background-color: #fafafa;
  font-size: 0.875rem;
}
.footer__wrapper {
  height: 24.625rem;
  padding: 4.0625rem 10.25rem 0;
}
.footer__block:nth-of-type(2) {
  width: 80%;
}
.footer .section--main {
  color: #837d87;
  font-weight: 700;
}
.footer__link {
  color: #2d2640;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
}
.footer__link:hover::after {
  width: 100%;
}
.footer__link::after {
  content: "";
  background-color: #2d2640;
  bottom: -10%;
  height: 1px;
  left: 0;
  position: absolute;
  transform-origin: center;
  transition: width 0.3s ease;
  width: 0;
}
.footer a:focus-visible {
  outline: 3px dashed #000000;
  outline-offset: 3px;
}

#navbox {
  display: none;
}

#navtoggle {
  background: url("../images/icon-hamburger.svg") no-repeat center center;
  cursor: pointer;
  display: none;
  height: 1.90625rem;
  width: 1.90625rem;
}

.navlink {
  color: #837d87;
  font-size: 0.875rem;
  font-family: "Karla", sans-serif;
  transition: color 0.3s ease, text-shadow 0.3s ease;
}
.navlink:hover {
  color: #2d2640;
  text-shadow: 0px 0 1px #2d2640;
}

.heading-title {
  color: #fafafa;
  font-size: clamp(2.875rem, 4.9vw, 4rem);
  font-family: "DM Serif Display", serif;
  line-height: 4rem;
  width: 80%;
  z-index: 10;
}

.gutter--flex {
  flex: 0 1 100%;
}
.gutter--main {
  gap: 0 2rem;
}

.section--main {
  align-items: flex-start;
}

.text-gray {
  color: #D5D1DC;
  z-index: 10;
}

.text-l-gray {
  color: #837d87;
}

.text-white {
  color: #fafafa;
}

.line-white {
  z-index: 10;
}
.line-white::before {
  background-color: #fafafa;
}

.line-black {
  z-index: 10;
}
.line-black::before {
  background-color: rgba(10, 10, 10, 0.3);
}

.block {
  align-items: center;
  margin-bottom: 4.75rem;
  position: relative;
}
.block::before {
  content: "";
  background-color: rgba(50, 50, 50, 0.15);
  height: 1px;
  bottom: -1.875rem;
  position: absolute;
  width: 100%;
}
.block__social {
  gap: 0 1rem;
}

.footer--s-link > path {
  transition: fill 0.3s ease;
}
.footer--s-link:hover > path {
  fill: #2d2640;
}

@media (max-width: 62.5em) {
  .header {
    z-index: 20;
  }
  .header__wrapper {
    padding: 0 1.5rem;
  }

  .navbar {
    background-color: #2b272f;
    left: 0;
    max-height: 0;
    overflow: hidden;
    position: absolute;
    top: 100%;
    transition: max-height 0.5s ease, visibility 0.5s ease;
    visibility: hidden;
    width: 100%;
  }
  .navbar > ul {
    flex-direction: column;
    gap: 2.375rem 0;
    height: 36.75rem;
    padding: 2.8125rem 1.5rem 0;
    text-align: center;
  }
  .navbar::after {
    content: "";
    background: url("../images/bg-pattern-mobile-nav.svg") no-repeat center bottom;
    background-size: 100% 13.625rem;
    height: 16.625rem;
    width: 100%;
    bottom: 0;
    position: absolute;
  }

  .hero__wrapper {
    padding: 6.5625rem 1.5rem 0;
  }

  .overview__wrapper {
    padding: 3.75rem 1.5rem 0;
  }
  .overview__wrapper::before {
    left: 1.5rem;
  }

  .cta__wrapper {
    padding: 0 1.5rem;
  }

  .footer__wrapper {
    padding: 4.0625rem 1.5rem 0;
  }

  #navbox {
    display: block;
    left: -10000px;
    top: auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
    position: absolute;
  }
  #navbox:focus + label {
    outline: 3px dashed black;
    outline-offset: 2px;
  }
  #navbox:checked ~ .navbar {
    max-height: 50rem;
    visibility: visible;
  }
  #navbox:checked ~ #navtoggle {
    background-image: url("../images/icon-close.svg");
  }

  #navtoggle {
    display: block;
  }

  .navlink {
    color: #FFFFFF;
    font-weight: 700;
    font-size: 1.25rem;
  }
  .navlink:hover {
    color: #FFFFFF;
  }

  .btn.btn--black {
    border: 1px solid #FFFFFF;
    color: #FFFFFF;
    display: block;
    width: 100%;
  }
}
@media (max-width: 46.875em) {
  .footer {
    background: url("../images/bg-pattern-footer-mobile.svg") no-repeat center top;
    background-size: contain;
  }
  .footer__wrapper {
    height: auto;
  }
  .footer__block:nth-of-type(2) {
    align-items: center;
    flex-direction: column;
    width: 100%;
  }
  .footer .section--main {
    align-items: center;
  }
  .footer .section--main {
    margin-bottom: 2.5rem;
  }

  .hero {
    margin-bottom: 6.75rem;
  }
  .hero__wrapper {
    flex-direction: column-reverse;
    height: auto;
    padding: 0;
  }
  .hero::before, .hero::after {
    display: none;
  }
  .hero__text {
    align-items: center;
    padding: 5.75rem 1.5rem;
    position: relative;
    text-align: center;
  }
  .hero__text::before {
    content: url("../images/bg-pattern-intro-left-mobile.svg");
    position: absolute;
  }
  .hero__text::after {
    content: url("../images/bg-pattern-intro-right-mobile.svg");
    bottom: -11.5rem;
    position: absolute;
    right: 0;
  }
  .hero--image {
    display: none;
  }
  .hero--image-mbl {
    display: block;
    height: 28.125rem;
    object-fit: cover;
  }

  .overview {
    text-align: center;
  }
  .overview .gutter {
    flex-direction: column;
    gap: 3.75rem 0;
  }
  .overview .section--main {
    align-items: center;
  }

  .cta {
    text-align: center;
  }
  .cta__wrapper {
    padding: 0;
  }
  .cta__holder {
    flex-direction: column;
    gap: 1.5rem 0;
    height: 21.5rem;
    justify-content: center;
    overflow: hidden;
    padding: 0 2.5rem;
  }
  .cta__holder::before {
    content: url("../images/bg-pattern-how-we-work-mobile.svg");
  }

  .line-white::before {
    background-color: transparent;
  }

  .line-black {
    align-items: center;
  }
  .line-black::before {
    left: 50%;
    transform: translateX(-50%);
  }

  .block {
    flex-direction: column;
    gap: 1.5rem 0;
  }
}